/**
 * Main CSS file for our plugin admin side.
 *
 * @package luna-core
 */
.nav-tabs {
  float: left;
  width: 100%;
  margin: 0;
  list-style-type: none;
  border-bottom: 1px solid transparent;
}

.nav-tabs > li {
  float: left;
  margin-bottom: -2px;
}

.nav-tabs > li > a {
  margin-right: 2px;
  line-height: 1.5;
  padding: 10px;
  border: 1px solid transparent;
  border-radius: 4px 0;
  float: left;
  text-decoration: none;
}

.nav-tabs > li > a:hover {
  border-color: #eee #eee #ddd;
}

.nav-tabs > li.active > a,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:focus {
  color: #555;
  cursor: default;
  background-color: white;
  border-bottom: 1px solid #ddd;
}

.tab-content > .tab-pane {
  display: none;
  width: 98%;
  float: left;
}

.tab-content > .tab-pane.active {
  display: block;
  padding: 10px;
  background-color: #fff;
  box-shadow: 0 1px 5px -2px rgba(0, 0, 0, 0.15);
}

div.ui-toggle {
  margin: 0;
  padding: 0;
}

div.ui-toggle input[type="checkbox"] {
  display: none;
}

div.ui-toggle input[type="checkbox"]:checked + label {
  border-color: #009eea;
  background: #009eea;
  box-shadow: inset 0 0 0 10px #009eea;
}

div.ui-toggle input[type="checkbox"]:checked + label > div {
  margin-left: 20px;
}

div.ui-toggle label {
  transition: all 200ms ease;
  display: inline-block;
  position: relative;
  user-select: none;
  background: #8c8c8c;
  box-shadow: inset 0 0 0 0 #009eea;
  border: 2px solid #8c8c8c;
  width: 40px;
  height: 20px;
  border-radius: 22px;
}

div.ui-toggle label div {
  transition: all 200ms ease;
  background: #ffffff;
  width: 20px;
  height: 20px;
  border-radius: 10px;
}

div.ui-toggle label:hover,
div.ui-toggle label > div:hover {
  cursor: pointer;
}

.cpt-table {
  width: 100%;
  border-spacing: 5px;
  text-align: left;
}

.cpt-table,
.cpt-table th,
.cpt-table td {
  border: 1px solid #ccc;
  border-collapse: collapse;
  padding: 10px;
}

.cpt-table th {
  background-color: #f5f5f5;
}

.text-center {
  text-align: center;
}
